<template>
  <div class="wrap">
    <div class="hearer"><p class="title">메모장</p></div><!-- // header -->
    <div class="contents">
      <div class="memoBox" v-if="mode ==='LIST'">
        <ul class="memoList">
          <li v-for="m in memos" :key="m">
            <table>
              <tr>
                <td>{{m.id}}</td>
                <td>{{m.content}}</td>
                <td>{{(m.regData)}}</td>
              </tr>
            </table>
          </li>
        </ul>
      </div><!--// v-if -->
      <div class="memoWrite" v-else>
        <textarea v-model="memo.content"></textarea>
        <div class="btns--wrap flex">
          <buttom type="button" class="btn save" @click="save()">save</buttom>
          <buttom type="button" class="btn cancle" @click="cancel()">cancel</buttom>
        </div>
      </div><!--// else -->
      <button type="button" class="btn add" @click="write()" v-if="mode === 'LIST'">+</button>
    </div><!-- // contents -->
  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'

export default {
  name: 'App',
  data(){
    return{
      mode: 'LIST',
      memo: {
        id: null,
        content: null,
        regData: null
      },
      memos:[
        // {
        //   id: 1,
        //   content: '할일',
        //   regData: new Date()
        // },
        // {
        //   id: 2,
        //   content: 'todo',
        //   regData: new Date()
        // },
        // {
        //   id: 3,
        //   content: 'study',
        //   regData: new Date()
        // },
        // {
        //   id: 4,
        //   content: 'vue',
        //   regData: new Date()
        // },
      ],
    }
  }, // data
  methods:{
    write(){
      this.mode = "write"
    },
    cancel(){
      this.mode = "LIST"
    },
    save(){
      let id = this.memos.length + 1;
      this.memos.push({
          id: id,
          content: this.memo.content,
          regData: new Date()
      });
      this.memo.content = null;
      this.mode = "LIST"
      localStorage.setItem('momos', JSON.stringify(this.memos)); // 로켈 스토리지에 json 형태로 저장
    },
  },
  created(){
    let memos = localStorage.getItem('momos');
    if(memos){
      this.memos = JSON.parse(memos);
    }
  },
  // components: {
  //   HelloWorld
  // }
}
</script>

